import style from "./PrivateOffice.module.css";
import { useSelector } from "react-redux";
import { useMemo } from "react";
import { ProfileData } from "./ProfileData";

export const PrivateOffice = () => {
  const offerBooked = useSelector((state) => state.booked);

  const offer = useMemo(() => {
    if (offerBooked.offer === 0) {
      return false;
    } else {
      return true;
    }
  }, []);

  return (
    <>
      <div className={style.wrap_data}>
        <h3 className={style.title}> </h3>
        <div className={style.wrapper_data}>
          <ProfileData />
        </div>
      </div>
      <div className={style.wrap}>
        <h3 className={style.title}> </h3>
        <div className={style.wrapper}>
          {offer ? (
            <div className={style.wrap_text}>
              <p>{offerBooked.room_name}</p>
              <p>  : {offerBooked.price} </p>
              <p>: {offerBooked.acc_name}</p>
              <p>{offerBooked.tariff_name}</p>
              <p> : {offerBooked.date_begin}</p>
              <p> : {offerBooked.date_end}</p>
              <p> : {offerBooked.check_in}</p>
              <p> : {offerBooked.check_out}</p>
              <p>: {offerBooked.nights}</p>
            </div>
          ) : (
            <p className={style.text}>  </p>
          )}
        </div>
      </div>
    </>
  );
};
